.services__link:hover .services__decorate {
  opacity: 0.8;
}

.services__decorate {
  position: absolute;
  content: "";
  width: 60px;
  height: 60px;
  bottom: 0;
  right: 0;
  background-color: #00b2ff;
  border-radius: 3px 0 0 0;
}
@media (max-width: 767px) {
  .services__decorate {
    width: 40px;
    height: 40px;
  }
}
.services__decorate::before, .services__decorate::after {
  position: absolute;
  content: "";
  width: 1px;
  height: 6px;
  background-color: #ffffff;
  left: 50%;
}
.services__decorate::before {
  top: calc(50% + 2px);
  transform: translate(-50%, -50%) rotate(45deg);
}
.services__decorate::after {
  top: calc(50% - 2px);
  transform: translate(-50%, -50%) rotate(-45deg);
}

